nav{background:rgba(0,0,0,0);position:fixed;width:100%;z-index:999}nav.scrolled{background-color:rgba(240,92,6,.8980392157)}nav .nav-container{max-width:var(--container-width-exlg);margin:0 auto}nav .nav-container::after{content:"";clear:both;display:table}nav .nav-container .logo{float:left}nav .nav-container .logo a{color:var(--color-black);font-size:1.68rem;font-weight:600;line-height:70px;padding-left:60px}nav .nav-container .icon,nav .nav-container input{display:none}nav .nav-container ul{float:right;margin-right:40px;list-style:none;position:relative}nav .nav-container ul li{float:left;display:inline-block;background:rgba(0,0,0,0);margin:0 5px;font-family:"GenJyuuBold","Noto Serif JP",serif}nav .nav-container ul li:hover>ul{top:70px;opacity:1;visibility:visible}nav .nav-container ul li a{color:var(--color-black);line-height:70px;text-decoration:none;font-size:18px;padding:8px 15px;transition:var(--transition);font-family:"GenJyuuBold","Noto Serif JP",serif}nav .nav-container ul li a:hover{color:var(--color-white);border-radius:5px;box-shadow:0 0 5px var(--color-yellow),0 0 10px var(--color-yellow)}nav .nav-container ul li .show{display:none}nav .nav-container ul ul{position:absolute;top:90px;border-top:3px solid var(--color-yellow);opacity:0;visibility:hidden;transition:top .3s}nav .nav-container ul ul.scrolled{background-color:rgba(240,92,6,.8980392157)}nav .nav-container ul ul li{position:relative;margin:0px;width:150px;float:none;display:list-item;border-bottom:1px solid rgba(0,0,0,.5)}nav .nav-container ul ul li a{line-height:50px}nav .nav-container ul ul li a:hover{box-shadow:none}nav .nav-container ul ul ul{border-top:none}@media all and (max-width: 968px){nav .nav-container .logo{width:100%}nav .nav-container .logo a{padding-left:30px}nav .nav-container span.cancel:before{content:"ï€"}nav .nav-container .icon{display:block;color:var(--color-black);position:absolute;top:0;right:40px;line-height:70px;cursor:pointer;font-size:25px}nav .nav-container [id^=btn]:checked+ul{display:none}nav .nav-container ul{margin-right:0px;float:left}nav .nav-container ul li{width:100%}nav .nav-container ul li a{transition:var(--transition)}nav .nav-container ul li a:hover{box-shadow:none}nav .nav-container ul li .show+a{display:none}nav .nav-container ul li .show{display:block;font-family:"GenJyuuBold","Noto Serif JP",serif;color:var(--color-black);font-size:18px;padding:0 15px;line-height:70px;cursor:pointer}nav .nav-container ul li .show:hover{color:var(--color-yellow)}nav .nav-container ul li [id^=btn-1]:checked~ul{display:flex;flex-direction:column}nav .nav-container ul ul{top:70px;border-top:0px;float:none;position:static;display:none;opacity:1;visibility:visible}nav .nav-container ul ul a{padding-left:40px}nav .nav-container ul ul li{margin-right:0px;float:left}nav .nav-container ul ul li{border-bottom:0px}}